.live-bar-container {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
    margin-top: 0px;
}

.live-bar {
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background-color: gray;
    transition:
        background-color 0.3s ease,
        box-shadow 0.3s ease,
        opacity 0.3s ease;
    opacity: 0.3;
}

/* Green glowing effect when API is working */
.live-bar.active {
    opacity: 1;
    background-color: #0ee41d;
    box-shadow: 0 0 10px rgba(0, 255, 0, 0.8);
    animation: blink 0.00001s infinite alternate;
}

/* Blinking red effect when API is down */
.live-bar.error {
    opacity: 1;
    background-color: #cc1203;
    box-shadow: 0 0 10px rgba(255, 0, 0, 0.8);
    animation: blink 0.00001s infinite alternate;
}

@keyframes blink {
    from {
        opacity: 1;
    }
    to {
        opacity: 0.3;
    }
}

/* Container for the spinner */
.loading-container {
    position: relative;
    display: inline-block;
    width: 100px;
    height: 100px;
}

.loading-spinner {
    border: 0px solid #f3f3f3; /* Light gray background color */
    border-top: 1px solid #cc1203; /* Red color for the spinner */
    border-radius: 50%; /* Makes it round */
    width: 110px; /* Size of the spinner */
    height: 110px; /* Size of the spinner */
    animation: spin 1s linear infinite; /* Spin animation */
}

.loading-spinner-log {
    border: 0px solid #f3f3f3; /* Light gray background color */
    border-top: 1px solid #cc1203; /* Red color for the spinner */
    border-radius: 50%; /* Makes it round */
    width: 40px; /* Size of the spinner */
    height: 40px; /* Size of the spinner */
    animation: spin 1s linear infinite; /* Spin animation */
}

