    margin-right: 10px;
    margin-left: 0;
    width: auto;
    white-space: nowrap;
    position: relative;
    overflow: hidden;
    transition: color 1s ease;
}

/* Animated background filling effect */
.running .status-label::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 0%;
    background-color: #49bb51; /* #1e5f30   Darker green for the filling */
    opacity: 0;
    animation: fillAnimationRunning 7s linear infinite;
}

/* Animation for background filling and opacity */
@keyframes fillAnimationRunning {
    0% {
        width: 0%;
        opacity: 0;
    }
    50% {
        width: 50%;
        opacity: 0.5;
    }
    100% {
        width: 100%;
        opacity: 0.6;
    }
}

