.left-side {
  grid-column: 1 / 2;
  grid-row: 2 / 3;
  display: grid;
  grid-template-rows: 34% 24% 37.2%;
  gap: 20px;
  height: 97%;
  min-width: 400px; /* <-- force consistent width even when empty */
  margin-top: 9px;
  margin-left: 9px;
}

.buttons-section-top,
.buttons-section-middle,
.buttons-section-bottom {
  border-radius: 6px;
  padding: 10px;
  width: 100%;         /* make them stretch to full width */
  min-width: 100%;     /* prevent shrinkage */
  box-sizing: border-box;
  transform: scale(1.05);
}



/* Right side PodStatus full height */
.podstatus-section { 
    grid-column: 2 / 3;
