}

@keyframes scaleUp {
    0% {
        opacity: 0;
        transform: translate(-50%, -50%) scale(0.45);
    }
    100% {
        opacity: 1;
        transform: translate(-50%, -50%) scale(1);
    }
}
/* Embed section when active embed */
.next-embed-section {
