/* logo wrper */

.logo-wrapper-section {
    display: flex;
    gap: 20px;
    align-items: flex-end; /* important for upward lift effect */
    justify-content: center;
    padding: 16px 0;
}

.logo-wrapper {
    position: relative;
    display: inline-block;
    transition: transform 0.2s ease;
}

.logo-wrapper::before {
    content: "";
    position: absolute;
    top: -6px;
    left: -6px;
    width: 39px;
    height: 39px;
    border-radius: 50%;
    pointer-events: none;
    opacity: 0;
    transition: opacity 0.2s ease-in-out;
}

.logo-wrapper:hover::before {
    opacity: 1;
    animation: rotate-border 1.4s linear infinite;
}

@keyframes rotate-border {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.logo {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    cursor: pointer;
    transition: transform 0.2s ease-in-out;
    object-fit: cover;
    background-color: #222222;
}

/* macOS hover effect: scale + lift */
.logo-wrapper:hover .logo {
    transform: scale(1.7) translateY(-8px);
}

/* Neighboring icons scale up slightly */
.logo-wrapper:hover ~ .logo-wrapper .logo {
    transform: scale(1) translateY(-2px);
}

.spinner-ring {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.spinner-ring.active::before {
    content: "";
    position: absolute;
    width: 30px;
    height: 30px;
    border-top: 2px solid #ffffdc;
    border-radius: 50%;
    animation: spin 1.2s linear infinite;
    z-index: 0;
}

.logo-center {
    z-index: 1;
    position: relative;
}

.logo {
    width: 24px;
    height: 24px;
    object-fit: contain;
    cursor: pointer;
    /* make it bold/fat visually if needed */
    filter: contrast(1.2) brightness(1.1);
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

/* Animation for dropdown effect with 5px margin */
@keyframes slideDown {
    from {
        transform: translateY(-100%) /* Starts off-screen, 1000% above */;
        opacity: 0;
    }
    to {
        transform: translateY(0); /* Ends 5px below its initial position */
        opacity: 1;
    }
}

/* Apply animation to the embed section */
.next-embed-section {
    animation: slideDown 0.8s ease-in-out forwards;
}



