.butterfly-animation {
  width: 200px;
  height: 200px;
  animation: flyAround 3s ease-in-out infinite, flap 0.3s ease-in-out infinite;
}

@keyframes flyAround {
  0% {
    transform: translate(0, 0) rotate(0deg);
  }
  25% {
    transform: translate(-10px, -20px) rotate(3deg);
  }
  50% {
    transform: translate(10px, -10px) rotate(-3deg);
  }
  75% {
    transform: translate(-5px, 10px) rotate(2deg);
  }
  100% {
    transform: translate(0, 0) rotate(0deg);
  }
}

@keyframes flap {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}



